import {
  IonContent,
  IonHeader,
  IonLabel,
  IonList,
  IonPage,
  IonToolbar,
  IonImg,
  IonItem,
  IonSlides,
  IonSlide,
  IonRange,
  IonIcon,
  IonAlert,
} from '@ionic/react';
import React, { useEffect, useState, createRef, useRef, useCallback } from 'react';
import './Dashboard.scss';

import logoSvg from '../../assets/logo.svg';
import { useSelector, useDispatch } from 'react-redux';
import { RootState } from '../../store';
import { AppDispatch } from '../../store/store';
import {
  updateCurrentProfile,
  updateTemperature,
  setTemperatures,
  lock,
  unlock,
} from '../../store/reducers/bleReducer';
import { thermometer } from 'ionicons/icons';
import { RangeChangeEventDetail } from '@ionic/core';
import { coverflowOptions } from './coverflowOptions';

const slideLabels = ['Low', 'Medium', 'High', 'F1', 'F2', 'Concentrates'];

const version = require('../../../package.json').version;

interface IDashboardProps {
  readonly displayLockDialog: boolean;
  readonly dismissLockDialogCallback: () => void;
}

const Dashboard: React.FC<IDashboardProps> = (props: IDashboardProps) => {
  const temperatures = useSelector((state: RootState) => state.ble.temperatures);
  const profile = useSelector((state: RootState) => state.ble.profile);
  const updating = useSelector((state: RootState) => state.ble.updating);
  const battery = useSelector((state: RootState) => state.ble.battery);
  const locked = useSelector((state: RootState) => (state.ble.status & 1) === 1);

  const dispatch = useDispatch<AppDispatch>();

  const [currentProfile, setCurrentProfile] = useState(-1);
  const [tempDialogOpen, setTempDialogOpen] = useState(false);
  const [tempChanging, setTempChanging] = useState(false);
  //const [initialized, setInitialized] = useState(false);

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const slider = createRef<any>();
  const currentTemp = useRef(0);
  const currentTempTimeout = useRef<NodeJS.Timeout>();

  const updateTemperatureDispatch = useCallback(
    (value: number) => {
      const newTemp = [...temperatures.slice(0, profile - 1), value, ...temperatures.slice(profile)];
      dispatch(updateTemperature(newTemp));
    },
    [temperatures, profile, dispatch],
  );

  useEffect(() => {
    if (slider.current) {
      slider.current.lockSwipes(updating);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [updating]);

  useEffect(() => {
    if (slider && slider.current && profile > 0) {
      try {
        if (slider.current) {
          slider.current.getActiveIndex().then((index: number) => {
            if (profile !== index + 1 && profile > 0 && slider.current) {
              slider.current.slideTo(profile - 1);
            }
          });
        }
      } catch {
        // ignore
      }
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [profile]);

  const slideChanged = async (): Promise<void> => {
    if (!slider.current) {
      return;
    }

    try {
      if (currentProfile === profile) {
        const index = await slider.current.getActiveIndex();
        if (index !== profile - 1) {
          const newProfile = await dispatch(updateCurrentProfile(index + 1));
          if (newProfile !== profile) {
            setCurrentProfile(newProfile);
          }
        }
      } else if (currentProfile !== profile) {
        setCurrentProfile(profile);
      }
    } catch {
      // ignore
    }
  };

  const tempChange = (ev: CustomEvent<RangeChangeEventDetail>): void => {
    if (profile !== 4 && profile !== 5) {
      return;
    }
    if (currentTempTimeout.current) {
      clearTimeout(currentTempTimeout.current);
    }
    const value = ev.detail.value as number;
    const newTemp = [...temperatures.slice(0, profile - 1), value, ...temperatures.slice(profile)];
    dispatch(setTemperatures(newTemp));
    currentTempTimeout.current = setTimeout(() => {
      if (currentTemp.current === value && !tempChanging) {
        dispatch(updateTemperature(newTemp));
      }
    }, 1000);
    currentTemp.current = value;
  };

  const tempChangeFinished = (ev: React.PointerEvent<HTMLIonRangeElement>): void => {
    if (profile !== 4 && profile !== 5) {
      return;
    }
    setTempChanging(false);
    const value = ev.currentTarget.value as number;
    const newTemp = [...temperatures.slice(0, profile - 1), value, ...temperatures.slice(profile)];
    dispatch(setTemperatures(newTemp));
    dispatch(updateTemperature(newTemp));
    currentTemp.current = value;
  };

  const getBatteryIcon = (): JSX.Element => {
    //    const batteryLevel = Math.round(battery / 25);
    return (
      <svg width={42} height={18} className="battery" key={`batteryIndicator_${battery}`}>
        <rect
          width={34}
          height={14}
          stroke="#CCC"
          strokeWidth={2}
          x={2}
          y={2}
          rx={2}
          ry={2}
          className="battery-container"
        />
        <rect
          height={12}
          width={Math.round((battery / 100) * 32)}
          x={3}
          y={3}
          rx={2}
          ry={2}
          className="battery-gauge"
        />
        <rect width={4} height={8} x={36} y={6} className="battery-button" />
        {/* <text x={4} y={13} className="battery-text">
        {battery.toFixed(0)}%
      </text> */}
      </svg>
    );
  };

  return (
    <IonPage className="dashboard">
      <IonLabel class="version-label">{version}</IonLabel>
      <IonHeader className="dashboard-header">
        <IonToolbar className="dashboard-header-toolbar">
          <div slot="start" className="dashboard-header-toolbar-battery-icon">
            {getBatteryIcon()}
          </div>
          <IonImg className="dashboard-header-toolbar-logo" src={logoSvg} />
          <IonIcon slot="end" />
        </IonToolbar>
      </IonHeader>
      <IonContent className="dashboard-content" scrollX={false} scrollY={false}>
        <IonList className="dashboard-content-list" lines="none">
          {/* <IonListHeader className="dashboard-content-list-header">
            <IonLabel>Temperature</IonLabel>
          </IonListHeader> */}
          <IonItem className="dashboard-content-list-item">
            <IonSlides
              options={{
                ...coverflowOptions,
                allowSlideNext: !updating,
                allowSlidePrev: !updating,
                initialSlide: profile > 0 ? profile - 1 : 0,
              }}
              pager={true}
              ref={slider}
              onIonSlideTransitionEnd={slideChanged}
            >
              {temperatures.map((temperature, index) => {
                return (
                  <IonSlide key={index} title={index.toString()}>
                    <div className="swiper-slide-content">
                      <svg viewBox="0 0 100 100">
                        <circle cx="50" cy="50" r="46" strokeWidth="1" className="outline" />
                        <circle cx="52" cy="50" r="46" strokeWidth="1" className="shadow-right" />
                        <circle cx="48" cy="50" r="46" strokeWidth="1" className="shadow-left" />
                        <circle cx="50" cy="50" r="44" stroke="#fafafa" strokeWidth="2" className="circle" />
                        <text
                          x="25"
                          y="58"
                          fill="#fafafa"
                          onClick={(): void => {
                            if (index === 3 || index === 4) {
                              setTempDialogOpen(true);
                            }
                          }}
                        >
                          {temperature.toFixed(0)}&deg;C
                        </text>
                      </svg>
                    </div>
                    <div className="swiper-slide-footer">
                      <div className="swiper-slide-footer-text">{slideLabels[index]}</div>
                    </div>
                  </IonSlide>
                );
              })}
            </IonSlides>
          </IonItem>
          <IonItem className="dashboard-content-list-footer" hidden={profile !== 4 && profile !== 5}>
            <IonRange
              disabled={updating}
              min={100}
              max={220}
              step={1}
              value={(profile === 4 || profile === 5) && temperatures.length === 6 ? temperatures[profile - 1] : 0}
              snaps={true}
              color="danger"
              onIonChange={tempChange}
              onPointerDown={(): void => setTempChanging(true)}
              onPointerUp={tempChangeFinished}
            >
              <IonIcon slot="start" size="small" color="danger" icon={thermometer} />
              <IonIcon slot="end" color="danger" icon={thermometer} />
            </IonRange>
          </IonItem>
          <IonItem className="dashboard-content-list-footer" hidden={profile === 4 || profile === 5}>
            &nbsp;
          </IonItem>
        </IonList>
      </IonContent>
      <IonAlert
        isOpen={tempDialogOpen}
        onDidDismiss={(): void => setTempDialogOpen(false)}
        header={'New Temperature'}
        inputs={[
          {
            name: 'temperature',
            type: 'number',
            label: 'Temperature',
            placeholder: '<New Value>',
            value: profile >= 1 && profile <= 6 ? temperatures[profile - 1] : 0,
            min: 100,
            max: 220,
          },
        ]}
        buttons={[
          {
            text: 'Cancel',
            role: 'cancel',
            cssClass: 'secondary',
          },
          {
            text: 'Ok',
            handler: (values): void => {
              const value = parseInt(values.temperature);
              if (value >= 100 && value <= 220) {
                updateTemperatureDispatch(value);
              }
            },
          },
        ]}
      />
      <IonAlert
        isOpen={props.displayLockDialog}
        onDidDismiss={(): void => props.dismissLockDialogCallback()}
        header={locked ? 'Unlock?' : 'Lock?'}
        message={`Would you like to ${locked ? 'unlock' : 'lock'} your Ghost MV1 vaporizer?`}
        buttons={[
          {
            text: 'Cancel',
            role: 'cancel',
            cssClass: 'secondary',
          },
          {
            text: 'Ok',
            handler: (): void => {
              if (locked) {
                dispatch(unlock());
              } else {
                dispatch(lock());
              }
            },
          },
        ]}
      />
    </IonPage>
  );
};

export default Dashboard;
